<template>
    <div class="about">
        <img src="../../assets/news-back.png" class="img"/>
        <div class="navigation">
            <img src="../../assets/home.png" class="home"/>
            <div class="m-l-7" style="cursor: pointer;" @click="navigator">{{ $t('home') }}</div>
            <div class="m-l-7"> > </div>
            <div class="m-l-7">{{ $t('pressCenter') }}</div>
        </div>
        <div class="news">
            <div class="company-news">{{ $t('companyNews') }}</div>
            <div class="company-en">COMPANY NEW</div>
            <div class="card">
                <div class="content" v-for="(item,index) in info.news" :key="index" @click="toDetaul('/newsDetail', index)">
                    <div class="time">
                        <div class="year">{{ timeSplit(item.date,0) }}</div>
                        <div class="date">{{ timeSplit(item.date,1) }}</div>
                    </div>
                    <div class="detail">
                        <div class="d-title">{{ item.title }}</div>
                        <div class="d-text">{{ cutOut(item.content) }}</div>
                    </div>
                    <img :src="item.image_url" class="news-img"/>
                </div>
            </div>
        </div>
    </div>
</template>
<script setup>
import { useRouter } from "vue-router"
import { useStore } from 'vuex';
import { getCurrentInstance, computed } from "vue";

const router = useRouter()
const store = useStore()
const indent = getCurrentInstance().appContext.config.globalProperties.$indent

const info = computed(() => store.state.info);
const navigator = () =>{
    router.push("/")
}

const cutOut = (value) => {
    const random = Math.floor(Math.random() * 51 + 100)
    if(value && value.length > random) {
        const result = indent(value.substring(0,random) + '...')
        return result
    }
    const result = indent(value)
    return result
}

const timeSplit = (time, type) =>{
    if (time) {
        let arry = time.split("-");
        if(type === 0) {
            return arry[0]
        } else {
            return `${arry[1]}-${ arry[2] }`
        }
    }
    return ""
}

const toDetaul = (path, index) => {
    router.push({
        path,
        query:{
            index
        }
    })
}
</script>
